
export class Vector3 {
    x: number;
    y: number;
    z: number;
    constructor(x: number = 0, y: number = 0, z: number = 0) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    magnitude(v?: Vector3) {
        if (v === undefined) { return Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z); }
        let x = v.x - this.x, y = v.y - this.y, z = v.y - this.z;
        return Math.sqrt(x * x + y * y + z * z);
    }

    normalize() {
        let mag = this.magnitude();
        this.x /= mag; this.y /= mag; this.z /= mag;
        return this;
    }
    set(x: number, y: number, z: number) {
        this.x = x; this.y = y; this.z = z;
        return this;
    }
    multiScalar(v: number) {
        this.x *= v; this.y *= v; this.z *= v; return this;
    }
    getArray() {
        return [this.x, this.y, this.z];
    }
    getFloatArray() {
        return new Float32Array([this.x, this.y, this.z]);
    }
    clone() {
        return new Vector3(this.x, this.y, this.z);
    }
}